.pagination {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 16px;
	flex: 0 0 auto;
}

.link_go {
	font-size: 24px;
	line-height: 33px;
	background-color: transparent;
	border: none;
	color: #474955;
}

.pagination_items {
	display: flex;
	padding: 0;
}

.pagination_item {
	color: #474955;
	font-size: 18px;
	font-weight: 700;
	line-height: 25px;
	font-style: italic;
	border: none;
	background-color: transparent;
	user-select: none;
}

.pagination_item + .pagination_item {
	margin-left: 14px;
}

.active {
	color: #7ebc3c;
}

@media (max-width: 590px) {
	.link_go {
		font-size: 21px;
	}

	.pagination_item {
		font-size: 16px;
	}
}

@media (max-width: 420px) {
	.pagination {
		padding: 0;
	}

	.link_go {
		font-size: 14px;
	}

	.pagination_item {
		font-size: 12px;
	}
}
